<template>
  <view class="uni_song" @click="onPlay">

    <view class="anim"
          :class="[isShow ? 'clickAnim' : '']">
    </view>

    <image :src="song.al.picUrl + '?param=70y70'"
           mode="widthFix"
           lazy-load
           class="song_img">
    </image>

    <view class="song_detail">
      <view class="song_name">{{song.name}}</view>
      <view class="singer">{{song.ar[0].name}}</view>
    </view>

    <uni-sound-wave v-if="getCurrentSong === song.id"
                    :run="getIsPlay">
    </uni-sound-wave>

    <button v-else class="flag iconfont icon-more"
            plain
            hover-class="hover"
            @click.stop="onMore">
    </button>

  </view>
</template>

<script>
import {BASE_URL} from "@/util";
import {mapState, mapMutations} from "vuex";
import types from "@/store/mutations-types";

import UniSoundWave from "@/components/soundWave/Uni-Sound-Wave";

export default {
  props: {
    song: {
      type: Object,
      default() {
        return {};
      }
    }
  },
  components: {
    UniSoundWave
  },
  data() {
    return {
      isShow: false
    }
  },
  computed: {
    ...mapState({
      getAudio: 'audio',
      getIsPlay: 'isPlay',
      getCurrentSong: 'currentSong',
      getRecentPlaySongs: 'recentPlaySongs'
    })
  },
  methods: {
    ...mapMutations([
        types.SET_SWITCH,
        types.SET_CURRENT_SONG,
        types.SET_RECENT_PLAY_SONGS
    ]),

    onPlay() {
      // 点击动画
      this.isShow = true;
      setTimeout(() => this.isShow = false, 300);

      let song = this.song, id = song.id;
      if (this.getCurrentSong === id) {
        this.getAudio.seek(0);
      } else {
        uni.request({
          url: `${BASE_URL}/song/url?id=${id}`
        }).then(res => {
          const [err, data] = res;
          let url = data.data.data[0].url;

          if (url) {
            let [...RPS] = this.getRecentPlaySongs, i;
            // 获取到歌曲url并设置audio和当前播放歌曲id
            this.getAudio.src = url;
            this[types.SET_CURRENT_SONG](id);

            // 查询最近播放列表中是否存在该歌曲，存在就从新放置到第一项，不存在就添加到第一项
            i = RPS.findIndex(item => item.id === id);
            if (i !== -1) {
              RPS.unshift(RPS.splice(i, 1));
            } else {
              RPS.unshift({
                id,
                picUrl: song.al.picUrl,
                name: song.name,
                singer: song.ar[0].name
              })
            }
            this[types.SET_RECENT_PLAY_SONGS](RPS)
          } else {
            uni.showToast({
              title: '歌曲链接失效，请换一首！',
              icon: 'loading',
            })
          }
        }).catch(err => {
          console.log(err);
        })
      }

    },

    onMore() {
      console.log(`more`);
    }
  }
}
</script>

<style scoped lang="scss">
  .anim{
    @include wh(100%, 100%);
    background-color: rgba(128, 128, 128, .5);
    position: absolute;
    left: 50%;
    top: 50%;
    transform: translate3d(-50%, -50%, 0);
    opacity: 0;
    transition: opacity .5s;
  }
  .clickAnim{
    opacity: 1;
  }

  .uni_song{
    position: relative;
    z-index: 999;
    display: flex;
    align-items: center;
    flex-wrap: wrap;
    margin-bottom: 50rpx;
    .song_img{
      @include wh(140rpx, 140rpx);
      border-radius: 12rpx;
      margin-right: 40rpx;
    }

    .song_detail{
      width: 55%;
      word-break: break-all;
      .song_name{
        color: $font-color-white;
        font-weight: bold;
        font-size: 38rpx;
      }
      .singer{
        color: $font-color-grey;
      }
    }

    .flag{
      font-size: 38rpx;
      color: $font-color-grey;
      border: none;
      border-radius: 50%;
      position: absolute;
      right: -2%;
    }
    .hover{
      @include hover();
    }
  }
</style>